import React from 'react'
import { graphql, Link } from 'gatsby'
import RunDetail from '../components/RunDetail'

// 
const RunDetailTemplate = ({ data }) => {
  const { markdownRemark } = data
  const { frontmatter, html } = markdownRemark
  
  //  frontmatter 
  const segmentData = {
    segment_efforts: frontmatter.segments || [],
    heartrate_data: frontmatter.heartrate_data || [],
    pace_data: frontmatter.pace_data || [],
    elevation_data: frontmatter.elevation_data || []
  }
  
  // splitsRunDetail
  // splitsJSON
  let splitsData = [];
  try {
    if (frontmatter.splits) {
      if (typeof frontmatter.splits === 'string') {
        splitsData = JSON.parse(frontmatter.splits);
      } else {
        splitsData = frontmatter.splits;
      }
    }
  } catch (e) {
    console.error('Error parsing splits data:', e);
  }

  return (
    <main style={{ maxWidth: '1400px', margin: '0 auto', padding: '20px' }}>
      <div style={{ marginBottom: '20px' }}>
        <Link 
          to="/" 
          style={{ 
            color: '#fc4c02', 
            textDecoration: 'none',
            display: 'inline-flex',
            alignItems: 'center',
            fontSize: '16px'
          }}
        >
           
        </Link>
      </div>
      
      {/* splitsRunDetail */}
      <RunDetail 
        runData={markdownRemark} 
        segments={segmentData} 
        splits={splitsData} 
      />
      
      {/* MarkdownRunDetail */}
    </main>
  )
}

export const pageQuery = graphql`
  query($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        date
        distance
        duration
        elevation
        avg_speed
        max_speed
        avg_pace
        max_pace
        avg_heartrate
        max_heartrate
        calories
        splits {
          distance
          elapsed_time
          moving_time
          average_speed
          pace
          average_heartrate
          elevation_difference
          split_number
        }
        segments {
          name
          distance
          elapsed_time
          moving_time
          average_heartrate
          max_heartrate
          average_grade
          maximum_grade
          elevation_difference
        }
      }
      fileAbsolutePath
    }
  }
`

export default RunDetailTemplate